
import _ from 'lodash';
import CompositionFunction from "@/components/CompositionFunction.vue";
import { CHARACTER_MASTER, ELEMENT_IMG_SRC, TAnyObject, TCharacterKey, ELEMENT_BG_COLOR_CLASS } from "@/master";
import { computed, defineComponent, onMounted, PropType, reactive, ref, watch } from "vue";
import { calculateMemberResult, getElementalResonance, TTeam, TTeamMemberResult } from "./team";
import MemberItem from "./MemberItem.vue";
import { calculateFormulaArray } from "@/calculate";
import { TStats } from "@/input";

export default defineComponent({
  name: "TeamItem",
  props: {
    team: { type: Object as PropType<TTeam>, required: true },
    selected: { type: Boolean, requied: true },
    displayStat: { type: String },
  },
  emits: ['click:edit', 'change:buildname', 'click:jump-to-rotation', 'update:member-result'],
  components: {
    MemberItem,
  },
  setup(props, context) {
    const { displayName } = CompositionFunction();

    const memberStats = reactive({} as TAnyObject);
    const memberResults = reactive({} as TTeamMemberResult);
    const selectedClass = computed(() => (props.selected ? ' selected' : ''));
    const editable = ref(false);
    const res = reactive({} as TAnyObject);
    const resKey = ['', '', '', '', '', '', '', ''];

    const characterOwnListStr = localStorage.getItem('');
    let characterOwnList: TAnyObject;
    if (characterOwnListStr) {
      characterOwnList = JSON.parse(characterOwnListStr);
    } else {
      characterOwnList = {};
    }

    const watchCount = ref(0);
    watch(props, async (newVal) => {
      watchFunc(newVal.team);
      watchCount.value++;
    });

    onMounted(() => {
      watchFunc(props.team);
    })

    async function watchFunc(team: TTeam) {
      const list = [];
      for (const member of team.members) {
        list.push({
          key: member.id,
          value: await calculateMemberResult(member, team),
        });
      }
      await Promise.all(list).then(results => {
        results.forEach(entry => {
          memberStats[entry.key] = entry.value?.statsInput?.statsObj;
          memberResults[entry.key] = entry.value;
        });
      });
      calculateStat();
      calculateRes();
      context.emit('update:member-result', team.id, memberResults);
    }

    function calculateStat() {
      const teamAdjustments: { [key: string]: TStats } = {};
      Object.keys(memberStats).forEach(key => {
        teamAdjustments[key] = {};
      })
      Object.keys(memberStats).forEach(key => {
        const memberResult = memberResults[key];
        const characterMaster = memberResult?.characterInput?.characterMaster;
        const weaponMaster = memberResult?.characterInput?.weaponMaster;
        if (characterMaster && characterMaster.) {
          for (const damageDetail of characterMaster.) {
            if (damageDetail.) continue;
            const value = calculateFormulaArray(damageDetail., memberResult.statsInput.statsObj, memberResult.damageResult, damageDetail., damageDetail.);
            const toStat = damageDetail..replace(/V[1-3]$/, '');
            Object.keys(memberResults).forEach(key2 => {
              if (key2 != key) { // 
                if (toStat in teamAdjustments) {
                  teamAdjustments[key2][toStat] += value;
                } else {
                  teamAdjustments[key2][toStat] = value;
                }
              }
            })
          }
        }
        if (weaponMaster && weaponMaster.) {
          for (const damageDetail of weaponMaster.) {
            if (damageDetail.) continue;
            const value = calculateFormulaArray(damageDetail., memberResult.statsInput.statsObj, memberResult.damageResult, damageDetail., damageDetail.);
            const toStat = damageDetail..replace(/V[1-3]$/, '');
            Object.keys(memberResults).forEach(key2 => {
              if (key2 != key) { // 
                if (toStat in teamAdjustments) {
                  teamAdjustments[key2][toStat] += value;
                } else {
                  teamAdjustments[key2][toStat] = value;
                }
              }
            })
          }
        }
      })
      for (const key of Object.keys(memberStats)) {
        if (key in teamAdjustments) {
          for (const stat of Object.keys(teamAdjustments[key])) {
            if (!memberStats[key]) continue;
            if (stat in memberStats[key]) {
              memberStats[key][stat] += teamAdjustments[key][stat];
            } else {
              memberStats[key][stat] = teamAdjustments[key][stat];
            }
          }
        }
      }
    }

    function initializeRes() {
      resKey.forEach(key => {
        res[key] = 0;
      });
    }
    initializeRes();

    // 
    function calculateRes() {
      initializeRes();
      const dmgElements: string[] = [];
      const artifactSet4s: string[] = [];
      Object.keys(memberResults).forEach(key => {
        const memberResult = memberResults[key];
        const characterMaster = memberResult?.characterInput?.characterMaster;
        const artifactSetMasters = memberResult?.characterInput?.artifactSetMasters;
        if (characterMaster) {
          if (characterMaster) {
            dmgElements.push(characterMaster.);
          }
          if (characterMaster.?.) {
            for (const damageDetail of characterMaster.?.) {
              if (damageDetail. && damageDetail..startsWith('') && damageDetail..endsWith('') && damageDetail.) {
                const element = damageDetail..replace(/^/, '').replace(/$/, '').replace(/$/, '');
                let value = 0;
                if (_.isNumber(damageDetail.)) {
                  value = damageDetail.;
                } else if (_.isPlainObject(damageDetail.)) {
                  const level = memberResult.characterInput?.;
                  if (level && level in damageDetail.) {
                    value = damageDetail.[level];
                  }
                }
                if (element === '') {
                  resKey.filter(s => s !== '').forEach(key => {
                    res[key] -= value;
                  })
                } else {
                  res[element] -= value;
                }
              }
            }
          }
          if (characterMaster.?.) {
            for (const damageDetail of characterMaster.?.) {
              if (damageDetail. && damageDetail..startsWith('') && damageDetail..endsWith('') && damageDetail.) {
                const element = damageDetail..replace(/^/, '').replace(/$/, '').replace(/$/, '');
                let value = 0;
                if (_.isNumber(damageDetail.)) {
                  value = damageDetail.;
                } else if (_.isPlainObject(damageDetail.)) {
                  const level = memberResult.characterInput?.;
                  if (level && level in damageDetail.) {
                    value = damageDetail.[level];
                  }
                }
                if (element === '') {
                  resKey.filter(s => s !== '').forEach(key => {
                    res[key] -= value;
                  })
                } else {
                  res[element] -= value;
                }
              }
            }
          }
          if (characterMaster.) {
            for (const damageDetail of characterMaster.) {
              if (damageDetail. && damageDetail..startsWith('') && damageDetail..endsWith('') && damageDetail.) {
                const element = damageDetail..replace(/^/, '').replace(/$/, '').replace(/$/, '');
                let value = 0;
                if (_.isNumber(damageDetail.)) {
                  value = damageDetail.;
                }
                if (element === '') {
                  resKey.filter(s => s !== '').forEach(key => {
                    res[key] -= value;
                  })
                } else {
                  res[element] -= value;
                }
              }
            }
          }
          let constellationLevel = 0;
          if (memberResult.characterInput?. !== undefined) {
            constellationLevel = memberResult.characterInput?.;
          } else if (characterMaster. in characterOwnList) {
            constellationLevel = characterOwnList[characterMaster.];
          }
          if (characterMaster.) {
            for (let i = 1; i <= constellationLevel; i++) {
              const constellationObj = characterMaster.[String(i)];
              if (constellationObj.) {
                for (const damageDetail of constellationObj.) {
                  if (damageDetail. && damageDetail..startsWith('') && damageDetail..endsWith('') && damageDetail.) {
                    const element = damageDetail..replace(/^/, '').replace(/$/, '').replace(/$/, '');
                    if (_.isNumber(damageDetail.)) {
                      if (element === '') {
                        resKey.filter(s => s !== '').forEach(key => {
                          res[key] -= damageDetail.;
                        })
                      } else {
                        res[element] -= damageDetail.;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        if (artifactSetMasters && artifactSetMasters.length == 2) {
          if (artifactSetMasters[0].key == artifactSetMasters[1].key) {
            if (!artifactSet4s.includes(artifactSetMasters[0].key)) {
              artifactSet4s.push(artifactSetMasters[0].key);
              if (artifactSetMasters[0]['4']?.) {
                artifactSetMasters[0]['4']?..forEach(damageDetail => {
                  if (damageDetail..startsWith('') && damageDetail..endsWith('') && damageDetail.) {
                    const element = damageDetail..replace(/^/, '').replace(/$/, '').replace(/$/, '');
                    res[element] -= Number(damageDetail.);
                  }
                })
              }
            }
          }
        }
      })
      if (dmgElements.filter(s => s === '').length >= 2) { // 
        res[''] -= -20;
      }
      if (dmgElements.includes('') && dmgElements.includes('')) { // 
        res[''] -= -40;
      }
      resKey.forEach(key => {
        if (key !== '' && !dmgElements.includes(key)) {
          res[key] = 0;
        }
      })
    }

    const elementalResonance = computed(() => getElementalResonance(props.team));

    const resonanceElementImgSrcs = computed(() => {
      const result: string[] = [];
      elementalResonance.value.forEach((entry) => {
        if (entry == "") {
          const members = props.team.members.filter((s) => s.name);
          if (members.length == 4) {
            members.forEach((member) => {
              const master = CHARACTER_MASTER[member.name as TCharacterKey];
              const src = (ELEMENT_IMG_SRC as any)[master.];
              result.push(src);
            });
          }
        } else {
          const src = (ELEMENT_IMG_SRC as any)[entry.replace("", "")];
          result.push(src);
          result.push(src);
        }
      });
      return result;
    });

    const description = computed(() => props.team.description ?? '')

    const resBgClass = (key: string) => {
      if (res[key] !== 0) {
        return key in ELEMENT_BG_COLOR_CLASS ? (ELEMENT_BG_COLOR_CLASS as any)[key] : 'physical';
      } else {
        return '';
      }
    }

    const editOnClick = () => {
      context.emit('click:edit', props.team.id);
    }

    const jumpToRotation = () => {
      context.emit('click:jump-to-rotation');
    }

    return {
      displayName,

      selectedClass,
      editable,
      memberStats,
      elementalResonance,
      resonanceElementImgSrcs,
      description,
      res,
      resKey,
      resBgClass,

      editOnClick,
      jumpToRotation,
    };
  },
});
